import React, { Component } from 'react';
import { createPortal } from 'react-dom';
import './Modal.scss';
import IconButton from '../IconButton/IconButton';
import { ReactComponent as CloseIcon } from '../../icons/close.svg';

const modalRoot = document.querySelector('#modal-root');

// –ø–µ—Ä–µ–∏—Å–ø–æ–ª—å–∑—É–µ–º–∞—è –º–æ–¥–∞–ª–∫–∞, –∫–∞–∫ children –ª—é–±–æ–π –∫–æ–Ω—Ç–µ–Ω—Ç –ø–µ—Ä–µ–¥–∞–µ–º
export default class Modal extends Component {
  componentDidMount() {
    window.addEventListener('keydown', this.handleKeyDown);
  }

  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleKeyDown);
  }

  // –∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É —ç—Å–∫–ø–µ–π–ø–∞
  handleKeyDown = e => {
    if (e.code === 'Escape') {
      this.props.onClose();
    }
  };

  //   –∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É –≤ –±—ç–∫–¥—Ä–æ–ø
  handelBackdropClick = e => {
    if (e.target === e.currentTarget) {
      this.props.onClose();
    }
  };

  render() {
    // "–ø–æ—Ä—Ç–∞–ª" –¥–ª—è –º–æ–¥–∞–ª–∫–∏ (—É–±–∏—Ä–∞–µ—Ç –∫–æ—Å—Ç—ã–ª—å —Å z-index –∏ –æ–≤–µ—Ä—Ñ–ª–æ—É)
    return createPortal(
      <div className="Modal__backdrop" onClick={this.handelBackdropClick}>
        <div className="Modal__content">
          <IconButton
            className="Modal__close IconButton"
            aria-label="Close Modal icon"
            onClick={this.props.onClose}
          >
            <CloseIcon width="32" height="32" fill="#black" />
          </IconButton>
          {this.props.children}
        </div>
      </div>,
      modalRoot,
    );
  }
}
